import React, {
  FormEvent,
  FormEventHandler,
  useMemo,
  useState,
} from 'react';

import type { Todo } from '~/models';

import { Container } from '~/components/atom';
import { FloatingCTA, TextInput } from '~/components/common';

type FormType = Omit<Todo, 'id'>;

export type TodoFormSubmitHandler = (
  formData: FormType,
  e: FormEvent<HTMLFormElement>,
) => void;

interface TodoFormProps {
  defaultValue?: Partial<FormType> | null;
  submitText?: string;
  onSubmit: TodoFormSubmitHandler;
}

export const TodoForm = (props: TodoFormProps) => {
  const { defaultValue, submitText, onSubmit } = props;

  const initialFormData = {
    title: '',
    content: '',
    dueDate: '',
    isCompleted: false,
  };
  const [form, setForm] = useState<FormType>({
    ...initialFormData,
    ...defaultValue,
  });

  const isSubmitDisabled = useMemo(() => {
    if (!form.title || !form.content) return true;
    return false;
  }, [form.title, form.content]);

  const handleChangeField = (
    key: keyof FormType,
    value: FormType[keyof FormType],
  ) => {
    setForm((prev) => {
      return {
        ...prev,
        [key]: value,
      };
    });
  };

  const handleSubmit: FormEventHandler<HTMLFormElement> = (e) => {
    e.preventDefault();
    onSubmit(form, e);
  };

  return (
    <Container>
      <form onSubmit={handleSubmit}>
        <TextInput
          block
          required
          label="제목"
          placeholder="제목 입력"
          name="title"
          value={form.title}
          onChange={(e) => handleChangeField('title', e.target.value)}
        />

        <TextInput
          block
          required
          label="내용"
          placeholder="내용을 입력해주세요"
          mode="textarea"
          name="content"
          value={form.content}
          onChange={(e) => handleChangeField('content', e.target.value)}
        />

        <TextInput
          block
          label="기한"
          type="date"
          name="dueDate"
          value={form.dueDate}
          onChange={(e) => handleChangeField('dueDate', e.target.value)}
        />

        <FloatingCTA
          type="submit"
          color="primary"
          disabled={isSubmitDisabled}
        >
          {submitText ?? '완료'}
        </FloatingCTA>
      </form>
    </Container>
  );
};

TodoForm.defaultProps = {
  defaultValue: null,
  submitText: '',
};
