import React, { useMemo } from 'react';
import { Container } from '../atom';
import { Header, HeaderBack, HeaderTitle, Headline } from '../common';

interface TodoHeaderProps {
  type: 'create' | 'modify';
}

export const TodoHeader = (props: TodoHeaderProps) => {
  const { type } = props;

  const heading = useMemo(() => {
    const prefix = '할 일';
    const title = type === 'create' ? '추가' : '수정';

    return `${prefix} ${title}`;
  }, [type]);

  return (
    <>
      <Header>
        <HeaderBack />
        <HeaderTitle>{heading}</HeaderTitle>
      </Header>

      <Container>
        <Headline>{heading}</Headline>
      </Container>
    </>
  );
};
